import React from "react";
import "./Cards.css";
import CardItem from "./CardItem";

function Cards() {
  return (
    <div className="cards">
      <h1>Check out these EPIC Products!</h1>
      <div className="cards__container">
        <div className="cards__wrapper">
          <ul className="cards__items">
            <CardItem
              src="images/img-9.jpg"
              text="Strato Slim Fit Crewneck Tech T-Shirt"
              label="New Arrival"
              path="/services"
            />
            <CardItem
              src="images/HazeShirt.png"
              text="Dri-FIT Static Training T-Shirt"
              label="Hot Selling"
              path="/services"
            />
          </ul>
          <ul className="cards__items">
            <CardItem
              src="images/img-3.jpg"
              text="Ponto Performance Crew Pullover"
              label="Sale"
              path="/services"
            />
            <CardItem
              src="images/img-4.jpg"
              text="Tech Zip Hoodie Jacket"
              label="Latest Trends"
              path="/products"
            />
            <CardItem
              src="images/hazedesignmock.jpg"
              text="Reign Short Sleeve T-Shirt"
              label="50 % Off"
              path="/sign-up"
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
